import {
	gametaskremindPages,
	gametaskremindAdd,
	gametaskremindUpdate,
	gametaskremindDelete,
//	gametaskremindDetail
} from 'api/api';
import storyform from './articleComponents/areaGameForm';
let pageData = {
	gameId:'',
	remindZhCnContent:'',
	remindJaJpContent:'',
	remindKoKrContent:'',
	remindEnUsContent:'',
	remindAct:1,
	distance:'',
}
export default {
    data() {
        return {
            searchValue: '',
            tableData:{},
            dialogVisible: false,
            dialogTyle: '',
            pageData: {
                ...pageData
            },
            page: {
                start: 0,
                length: 10,
                draw: 1,
            },
            currentPage: 1,
            pageSizes: [10, 20, 30],
        }
    },

    components: {
        storyform
    },
    created() {
        this.init()
    },
     watch: {
      searchValue(value){
        if(!value){
          gametaskremindPages(this.page).then(res => {
            	this.tableData = res.data.obj
            })
        }
      }
    },
    methods: {
        init() {
            gametaskremindPages(this.page).then(res => {
            	this.tableData = res.data.obj
            })
        },
        onQuery() {
        	if(this.searchValue){
	          gametaskremindPages({...this.page,gameName:this.searchValue}).then(res => {
					this.tableData = res.data.obj
				})
	        }
        },
        showDilog(type, obj=pageData) {
            this.dialogVisible = true
            this.dialogTyle = type
           
                this.pageData = { ...obj }
            

        },
        onSubmit() {
            this.$refs.storyForm.validate(valid => valid && this.commitForm())
        },
        commitForm() {
        	for(let key in this.pageData ){
        		if(key.indexOf('Time')>-1){
        			delete  this.pageData[key]
        		}
        	}
            if (this.dialogTyle === 'edit') {
	
                gametaskremindUpdate(this.pageData).then(res => {
                    this.init()
                    this.dialogVisible = false
                })
            } else if (this.dialogTyle === 'add') {
                gametaskremindAdd(this.pageData).then(res => {
                    this.init()
                    this.dialogVisible = false
                })
            }
        },
        handleDelete(obj) {
            this.$confirm(this.$t('commom.deleConfim'), '', {
                confirmButtonText: this.$t('commom.sure'),
                cancelButtonText: this.$t('commom.cancel'),
                type: 'warning'
            }).then(() => {
            	
                gametaskremindDelete({
                    remindId: obj.remindId
                }).then(res => {
                    this.init()
                })

            }).catch(res => {

            })


       },
       getpageData(){
	       	gametaskremindPages({...this.page,gameName:this.searchValue}).then(res => {
				this.tableData = res.data.obj
			})
        },
		handleSizeChange(val) {
			this.page.length = val
			this.page.start = (this.currentPage - 1) * this.page.length
	        	this.getpageData()
		},
		handleCurrentChange(val) {
			this.currentPage = val;
			this.page.start = (this.currentPage - 1) * this.page.length
			setTimeout(()=>{
	        	this.getpageData()
	        },0)
		},
     
    }
}



